.port__Header{
    padding:2rem 0;
}
.nav-active {
	overflow: hidden;
}
.port__Menu{
    display: flex;
    padding:0;
    list-style: none;
}
.port__Menu--item {
    margin-right: 30px;
}
.port__Menu--item a{
    font-size:3.5rem;
    color:var(--color-white);
    padding:10px 15px;
    transition: 100ms all linear;
    will-change: color;
}
.port__Menu--item a:hover {
    color: var(--color-accent);
}
.port__theme--Switcher {
	cursor: pointer;
	position: relative;
	z-index: 999;
}
.port__Sticked--Header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	transform: translateY(-100%);
	transition: 300ms all ease;
	background-color: rgba(0,0,0,.8);
	z-index: 99;
}
.port__Nav--Sticked .port__Sticked--Header {
    transform: translateY(0);
}
.nav-active .port__Sticked--Header .port__Menu {
	display: none;
}
.port__Sticked--Header .port__theme--Switcher svg {
    width: 8rem;
    height: auto;
}
.port__Nav--Sticked .port__Sticked--Header .port__Header {
    padding: 1rem 0;
}
.port__Sticked--Header .port__Menu--item a {
    font-size: 2.5rem;
    padding: 10px 0;
}
.theme-light .port__Sticked--Header {
    background-color: rgba(255,255,255,0.95);
    box-shadow: 0 -2px 16px rgb(0 0 0 / 20%);
}
path.port__torch--Light {
    stroke: var(--color--secondary);
}
.theme-light .port__torch--Light {
    stroke: var(--color-accent);
}
.port__Hamburger {
	display: inline-block;
	width: 50px;
	height: 50px;
	cursor: pointer;
	z-index: 999;
	position: relative;
}
.port__Hamburger svg {
	width: 100%;
	height: auto;
}
.theme-light .port__Hamburger path {
	stroke: var(--color-white);
}

@media screen and (min-width:767px){
    .port__Hamburger {
        display: none;
    }
}
@media screen and (max-width:1200px){
  .port__Menu--item a {
    padding: 0.5rem;
    font-size: 2.5rem;
  }
  .port__theme--Switcher svg {
    width: 10rem;
    height: auto;
  }
  }

  @media screen and (max-width:767px){
    .port__theme--Switcher svg {
        width: 7rem;
    }
    .port__Hamburger svg {
        position: absolute;
        top: 10px;
        transition: 300ms opacity linear;
        transition-delay: 400ms;
    }
    .nav-active .port__Hamburger svg:first-child {
        opacity: 0;
        visibility: hidden;
    }
    .nav-active .port__CloseMenu {
        opacity: 1;
        visibility: visible;
        z-index: 999;
        width: 40px;
        height: auto;
    }
    .port__CloseMenu {
        opacity: 0;
        visibility: hidden;
    }
    .port__Menu--item a {
        font-size: 3.5rem;
        white-space: pre;
        transition: 100ms all linear;
        display: inline-block;
    }
    .port__Menu--item a:hover {
        transform:translateY(-5px);
    }
    /*Mobile menu*/
    .port__Header::before,
    .port__Header::after {
        content: "";
        position: fixed;
        width: 100vw;
        height: 100vh;
        background: var(--caolor-dark);
        border-bottom-right-radius: 200%;
        z-index: -1;
        transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
        transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
        transform: translateX(-100%) translateY(-100%);
        z-index: 99;
        top:0
    }
    .port__Header::before {
        transition-delay: .2s;
        background-color: var(--color-accent);
    }
    body.nav-active .port__Header::before,
    body.nav-active .port__Header::after {
        transform: translateX(0%) translateY(0%);
        border-radius: 0;
    }
    body.nav-active .port__Header::before {
        transition-delay: 0s;
    }
    .port__Menu {
        display: flex;
        position: fixed;
        flex-direction: column;
        top: 50%;
        z-index: 999;
        left: 50%;
        transform: translate(-50% , -50%);
        visibility: hidden;
    }
    .port__Menu--item {
        position: relative;
        display: block;
        transition-delay: 0.8s;
        opacity: 0;
        text-align: center;
        color: #fff;
        overflow: hidden;
        font-weight: 900;
        line-height: 1.15;
        transform: translate(100px, 0%);
        transition: opacity .2s ease, transform .3s ease;
        margin-top: 0;
        margin-bottom: 1.5rem;
        visibility: hidden;
    }
    body.nav-active .port__Menu--item {
        opacity: 1;
        visibility: visible;
        transform: translateX(0%);
        transition: opacity .3s ease, transform .3s ease, color .3s ease;
    }
    body.nav-active .port__Menu--item:nth-child(1) {
        transition-delay: 0.8s;
    }
    body.nav-active .port__Menu--item:nth-child(2) {
        transition-delay: 0.9s;
    }
    body.nav-active .port__Menu--item:nth-child(3) {
        transition-delay: 1s;
    }
    body.nav-active .port__Menu--item:nth-child(4) {
        transition-delay: 1.1s;
    }
  }


.port__Section.port__hero {
    position: relative;
}
.port__Hero--image {
    display: flex;
