}
.Typewriter {
    font-size: 5rem;
}
svg path.port_Path--Accent-Arrow {
    stroke-dasharray: 1000;
    stroke-dashoffset: -1000;
    animation: 500ms ease-in-out svg-path-reverse 1 ;
}
svg:hover path.port_Path--Accent-Arrow {
    animation: 500ms ease-in-out svg-path 1;
    animation-fill-mode: forwards;
}
svg path.port_Path--Accent{
    transition: 500ms all linear;
}
svg:hover path.port_Path--Accent {
    fill:var(--color-accent);
}
@keyframes svg-path {
	0% {
		stroke-dashoffset: -1000;

	}
	40% {
		stroke-dashoffset: -1300;
		
	}
	50% {
		stroke-dashoffset: -1550;
		
	}
	100% {
		stroke-dashoffset: -1730
	}
}
@keyframes svg-path-reverse {
	0% {
		stroke-dashoffset: -1730;

	}
	40% {
		stroke-dashoffset: -1550;
		
	}
	50% {
		stroke-dashoffset: -1300;
		
	}
	100% {
		stroke-dashoffset: -1000
	}
}
