section#my-Works {
    overflow: hidden;
}
.port__Approach--Container {
    position: relative;
    width: 1000px;
    height: 1000px;
    margin: 0 auto;
}
.port__Approach {
    border: 1px dashed var(--color--secondary);
    border-radius: 50%;
    margin: 0 auto;
    position: absolute;
    width: 100%;
    height: 100%!important;
}
h4.port__blob--Eyebrow {
    position: absolute;
    font-size: 4rem;
    top: -11px;
    color: var(--color-accent)
}
.porto__Approach--title {
    color: transparent;
    -webkit-text-stroke: 0.1rem;
    -webkit-text-stroke-color: var(--color--secondary);
    font-size: 3rem;
    max-width: 167px;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
[class*="port__Approach--item--"]{
    -webkit-animation-name: spin;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
}
.port__Approach--item-wrapper {
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50% ,-50%);
    position: absolute;
}
.port__Approach--item-wrapper-1 {
    width: 80%;
    height: 80%;
}
.port__Approach--item-wrapper-2 {
    width: 60%;
    height: 60%;
}
.port__Approach--item-wrapper-3 {
    width: 40%;
    height: 40%;
}
.port__Apprach--Data {
    position: absolute;
    height: 150px;
    width: 150px;
    top: 50%;
    left: -7%;
    border-radius: 50%;
    color: #dddddd;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-animation-name: spin-reverse;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
}
.port__Approach--item--1 .port__Apprach--Data {
    top: -8%;
    left: 50%;
}
.port__Approach--item--2 .port__Apprach--Data {
    top: 0;
    left: 7%;
}
.port__Approach--item--3 .port__Apprach--Data {
    top: 0;
    left: 65%!important;
}
.port__Approach--item-wrapper-0 .port__Apprach--Data,
.port__Approach--item--0{
    -webkit-animation-duration: 90s;
}
.port__Approach--item-wrapper-1 .port__Apprach--Data,
.port__Approach--item--1{
    -webkit-animation-duration: 60s;
}
.port__Approach--item-wrapper-2 .port__Apprach--Data,
.port__Approach--item--2{
    -webkit-animation-duration: 30s;
}
.port__Approach--item-wrapper-3 .port__Apprach--Data,
.port__Approach--item--3{
    -webkit-animation-duration: 15s;
}
.port__Apprach--Data h3 {
    position: absolute;
}
@keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(-360deg);}
}

@keyframes spin-reverse {
    from {transform:rotate(-360deg);}
    to {transform:rotate(0deg);}
}
